<template>
  <mu-row class="profile">
    <mu-col width="5" tablet="25" desktop="25"></mu-col>
    <mu-col width="100" tablet="50" desktop="50">
      <mu-card class="profile">
        <mu-card-header>
          <mu-avatar :src="info.avatar" slot="avatar"/>
        </mu-card-header>
        <mu-divider/>
        <mu-text-field label="" hintext="32"
                       icon="account_circle" type="text"
                       :errorText="error_username"
                       v-model="info.username" labelFloat/>
        <br/>
        <mu-text-field label="" hintText=""
                       icon="mail" type="email"
                       :errorText="error_email"
                       v-model="info.email"
                       :errorColor="EmailErrorColor"
                       labelFloat/>
        <a v-show="showAuth" @click="emailAuth"></a>
        <br/>
        <mu-text-field label="" hintText="64" icon="place" type="text" v-model="info.location" labelFloat/>
        <br/>
        <mu-text-field
          label="" icon="perm_contact_calendar"
          type="text" multiLine :rows="3"
          :rowsMax="6" v-model="info.about_me" labelFloat/><br/>
        <mu-card-actions style="text-align: right">
          <mu-raised-button label="" @click="submit" primary/>
        </mu-card-actions>
      </mu-card>
      <mu-card style="margin-top: 10px">
        <mu-text-field label="" hintText="8-20" icon="lock" type="password" :errorText="error_password" v-model="old_password" labelFloat/><br/>
        <mu-text-field label="" hintText="8-20" icon="lock" type="password" :errorText="error_password" v-model="new_password" labelFloat/><br/>
        <mu-card-actions style="text-align: right">
          <mu-raised-button label="" @click="submitPassword" primary/>
        </mu-card-actions>
      </mu-card>
      <mu-card style="margin-top: 10px">
        <mu-text-field icon="visibility" label="" v-model="info.last_seen" disabled fullWidth/><br/>
        <mu-text-field icon="watch_later" label="" v-model="info.member_since" disabled fullWidth/><br/>
      </mu-card>
    </mu-col>
    <mu-col width="5" tablet="25" desktop="25"></mu-col>
    <mu-toast v-if="toast" :message="message" @close="hideToast"/>
  </mu-row>
</template>

<script>
  import api from '../services/api'
  export default {
    data: function () {
      return {
        info: '',
        toast: false,
        error_username: '',
        error_email: '',
        error_password: '',
        EmailErrorColor: 'orange',
        message: '',
        old_password: '',
        new_password: ''
      }
    },
    mounted: function () {
      this.$http.get(
        api.user,
        {
          headers: {
            Authorization: 'token ' + this.$cookie.get('token')
          }
        }
      ).then((response) => {
        this.info = response.data
        if (this.info.confirmed === 'False') {
          this.error_email = ''
        }
      }, (response) => {
        this.toast = true
        if (this.toastTimer) clearTimeout(this.toastTimer)
        this.toastTimer = setTimeout(() => { this.toast = false }, 2000)
        this.$router.push({name: 'index'})
      })
    },
    methods: {
      hideToast () {
        this.toast = false
        if (this.toastTimer) clearTimeout(this.toastTimer)
      },
      submit: function () {
        if (!this.info.username) {
          this.error_username = ''
          return
        }
        if (!this.info.email) {
          this.error_email = ''
          return
        }
        this.$http.put(
          api.user,
          {
            email: this.info.email,
            username: this.info.username,
            about_me: this.info.about_me,
            location: this.info.location
          }
        ).then((response) => {
          this.initData()
          this.showToast('')
        }, (response) => {
          console.log(response)
        })
      },
      submitPassword: function () {
        if (!this.old_password) {
          this.showToast('')
          return
        }
        if (!this.new_password) {
          this.showToast('')
          return
        }
        if (this.old_password === this.new_password) {
          this.showToast('')
          return
        }
        this.$http.post(
          api.password,
          {
            old_password: this.old_password,
            new_password: this.new_password
          }
        ).then((response) => {
          this.showToast('')
          this.$store.commit('logout')
          this.$store.commit('permission', 0)
          this.$cookie.delete('token')
          localStorage.removeItem('login')
          this.$router.push({name: 'login'})
        }, (response) => {
          this.showToast('')
          console.log(response)
        })
      },
      showToast: function (msg) {
        console.log(msg)
        this.message = msg
        this.toast = true
        if (this.toastTimer) clearTimeout(this.toastTimer)
        this.toastTimer = setTimeout(() => {
          this.toast = false
          this.message = ''
        }, 2000)
      },
      emailAuth: function () {
        this.$http.get(api.emailAuth).then((response) => {
          this.showToast('')
        })
      },
      initData: function () {
        this.$http.get(
          api.user,
          {
            headers: {
              Authorization: 'token ' + this.$cookie.get('token')
            }
          }
        ).then((response) => {
          this.info = response.data
          if (this.info.confirmed === 'False') {
            this.error_email = ''
          }
        }, (response) => {
          this.toast = true
          if (this.toastTimer) clearTimeout(this.toastTimer)
          this.toastTimer = setTimeout(() => { this.toast = false }, 2000)
          this.$router.push({name: 'index'})
        })
      }
    },
    computed: {
      showAuth: function () {
        if (this.info.confirmed === 'False') {
          return true
        }
      }
    }
  }
</script>

<style>
  .profile {
    justify-content: center !important;
    margin-top: 10px;
  }
</style>



