<template>
  <div class="parent">
    <mu-row class="child" gutter>
      <mu-col width="5" tablet="25" desktop="25"></mu-col>
      <mu-col width="90" tablet="50" desktop="50">
        <mu-card>
          <mu-text-field label="" hintext="32" icon="account_circle" type="text" :errorText="error_username" v-model="username" labelFloat/><br/>
          <mu-text-field label="" hintText="" icon="mail" type="email" :errorText="error_email" v-model="email" labelFloat/><br/>
          <mu-text-field label="" hintText="8-20" icon="lock" type="password" :errorText="error_password" v-model="password" labelFloat/><br/>
          <mu-text-field label="" hintText="64" icon="place" type="text" v-model="location" labelFloat/><br/>
          <mu-text-field label="" icon="perm_contact_calendar" type="text" multiLine :rows="3" :rowsMax="6" v-model="about_me" labelFloat/><br/>
          <mu-card-actions>
            <mu-raised-button label="" v-on:click="register" primary/>
          </mu-card-actions>
        </mu-card>
      </mu-col>
      <mu-col width="5" tablet="25" desktop="25"></mu-col>
    </mu-row>
  </div>
</template>

<script>
  import api from '../services/api'
  export default {
    data: function () {
      return {
        username: '',
        email: '',
        location: '',
        password: '',
        about_me: '',
        error_username: '',
        error_email: '',
        error_password: ''
      }
    },
    methods: {
      register: function () {
        let flag = true
        if (!this.username) {
          this.error_username = ''
          flag = false
        }
        if (!this.email) {
          this.error_email = ''
          flag = false
        }
        if (!this.password) {
          this.error_password = ''
          flag = false
        }
        if (!flag) return
        this.$http.post(
          api.user,
          {
            email: this.email,
            password: this.password,
            username: this.username,
            location: this.location,
            about_me: this.about_me
          }
        ).then((response) => {
          this.$cookie.set(
            'token',
            response.data.token,
            {expires: response.data.expiration + 's'}
          )
          this.$router.push({name: 'index'})
        }, (response) => {
          alert('fail')
        })
      }
    }
  }
</script>

<style scoped>
  .parent {
    /*width: 50%;*/
    text-align: center;
    margin-top: 8px;
  }
</style>



